.gpt3__possibility {
    display: flex;
    flex-direction: row;
}
.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;

    margin-right: 2rem;
}

.gpt3__possibility-image img {
    width: 100%;
    height: 100%;

    margin: 4rem;
}

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;

    margin-left: 3rem;
    margin-right: 3rem;
}

.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #71E5FF;
    
}
.gpt3__possibility-content h4:last-child {
    color: #FF8A71;
}
.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;

    margin: 1rem 0;
}
.gpt3__possibility-content p {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    color: var(--color-text);

    margin-bottom: 2rem;
    margin-right: 2rem;
}

@media screen and (max-width: 950px) {
    .gpt3__possibility {
        flex-direction: column;
    }
    .gpt3__possibility-image {
        margin: 1rem 0;
        margin-left: -2rem;
        margin-right: 6rem;
    }

    .gpt3__possibility-content {
        margin-top: 2rem;
    }
}