.gpt3__blog-container-article {
  width: 100%;
  height: 100%;
  

  display: flex;
  flex-direction: column;
  background: var(--color-footer);
}

.gpt3__blog-container-article__image {
  width: 100%;
  height: 100%;
  

  background: var(--color-footer);
}

.gpt3__blog-container-article__image img{
  width: 100%;
  height: 100%;
}

.gpt3__blog-container-article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding: 1rem 1.5rem;
  height: 100%;
}

.gpt3__blog-container-article-content p {
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 11.649;
    line-height: 35px;
    color: #FFFFFF;

}
.gpt3__blog-container-article-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25.11;
    line-height: 30px;
    color: #FFFFFF;

    margin-bottom: 5rem;
    cursor: pointer;

}
.gpt3__blog-container-article-content p:last-child {
    cursor: pointer;
}

@media screen and (max-width: 550px) {
    .gpt3__blog-container-article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}
